<template>
  <div class="record__content">
    <div class="record__baseinfo">
      <dividerTitle :title="'基本信息'"></dividerTitle>
      <editForm
        :formConfig="formConfig"
        ref="editForm"
        :formData="editFormData"
      >
        <template #orderNo="{ editFormInfo }">
          <el-input
            placeholder="请选择入库单号"
            v-model="editFormInfo.orderNo"
            readonly
            :disabled="pageType == 'details' && !isWorkFlowEdit"
            size="mini"
            class="input-with-select"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click.stop="getGoodsModel"
            ></el-button>
          </el-input>
        </template>
        
        <template #serviceOrderNo="{editFormInfo}">
          <el-input
            placeholder="请选择服务工单号"
            v-model="editFormInfo.serviceOrderNo"
            readonly
            :disabled="pageType == 'details' && !isWorkFlowEdit"
            size="mini"
            class="input-with-select"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click.stop="getServiceType"
            ></el-button>
          </el-input>
        </template>
      </editForm>
    </div>
    <div
      class="process_score"
      v-if="editFormData.status && editFormData.status == 1"
    >
      <dividerTitle :title="'评分'"></dividerTitle>
      <processScore
        v-if="showScore"
        :scoreSetting="scoreSetting"
        :hideBtn="scoreHideBtn"
        :showScore.sync="showScore"
      ></processScore>
    </div>
    <div class="record__attach">
      <dividerTitle :title="'附件'"></dividerTitle>
      <attachTable
        :attachSetting="attachSetting"
        :hideBtn="hideBtn"
        ref="attachFile"
      ></attachTable>
    </div>

    <div
      class="record__workflow"
      v-if="
        pageType == 'details' &&
        editFormData.status &&
        editFormData.status !== 2
      "
    >
      <dividerTitle :title="'工作流'"></dividerTitle>
      <workFlow
        :currentFlowData="editFormData"
        :beforeSubmit="beforeSubmit"
        :otherParams="workFlowParams"
        @submitSuccess="workFlowSuccess"
      ></workFlow>
    </div>

    <instockSelect
      v-if="showSelete"
      :hadSeleteData="hadSeleteData"
      @changeSelete="selectInstockId"
      :showSelete.sync="showSelete"
    ></instockSelect>
    
    <serviceNumberSelect
      v-if="serviceNumberSelete"
      :hadSeleteData="hadSeleteData"
      @changeSelete="changeServiceType"
      :showSelete.sync="serviceNumberSelete"
      :userID="employeeId"
    ></serviceNumberSelect>
  </div>
</template>

<script>
import {
  getDetailsApi, //获取详情
} from "@/api/itOperations/spareRecipients";
export default {
  props: {
    formData: {
      type: Object,
      default: new Object(),
    },
    pageType: {
      type: String,
      default: "",
    },
    defaultSet: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      // userInfo: this.$store.getters.userInfo,
      hadSeleteData: [],
      employeeId: null,
      showSelete: false,
      serviceNumberSelete:false,
      editFormData: { suitableWorkArr: [] },
      trainingType: {},
      formConfig: {
        itemArr: [
          
          {
            label: "领用人",
            placeholder: "请选择领用人",
            prop: "staffunName",
            idsStr: "employeeId",
            type: "user",
            trigger: "change",
            required: true,
            methods: (obj) => {
              let { orgNodeName, orgNodeCode, mobile, id } = obj[0];
              this.employeeId = id;
              this.$refs.editForm.setFormItem("depatName", orgNodeName);
              this.$refs.editForm.setFormItem("depatCode", orgNodeCode);
              // this.$refs.editForm.setFormItem("phone", mobile);
            },
          },
          {
            label: "领用部门",
            prop: "depatName",
            id: "depatCode",
            // required: true,
            disabled: true,
            placeholder: "选择领用人后自动带出",
            type: "text",
          },
          // {
          //   label: "领用人",
          //   prop: "staffunName",
          //   id: "employeeId",
          // },
          // {
          //   label: "所属部门",
          //   prop: "depatName",
          //   id: "depatCode",
          // },
          {
            label: "入库单号",
            prop: "orderNo",
            required: true,
            span: 8,
            // disabled: true,
          },
          {
            type: "input",
            label: "备件型号",
            placeholder: "选择入库单号后自动带出",
            prop: "goodsModel",
            id:"instockId",
            // required: true,
          },
          {
            type: "input",
            label: "备件类型",
            placeholder: "选择备件型号后自动带出",
            prop: "assetTypeName",
            id:"assetTypeId",
            // required: true,
            disabled: true,
          },
          {
            label: "服务工单号",
            prop: "serviceOrderNo",
            idsStr: "serviceId", //服务工单id
            type: "text"
            // required: true
          },
          {
            label: "服务类型",
            prop: "serviceType",
            type: "text",
            placeholder: "选择服务工单号后自动填入",
            disabled: true
            // required: true
          },
          // {
          //   type: "input",
          //   label: "库存数量",
          //   prop: "maxNum",
          //   placeholder: "选择备件型号后自动带出",
          //   disabled: true,
          //   // isHide:true
          // },
          {
            label: "领用数量",
            prop: "qty",
            type: "el-number",
            required: true,
            min: 0,
            max: 9999,
            precision: 0,
            step: 1,
          },
          {
            label: "领用说明",
            prop: "remark",
            type: "textarea",
            span: 24,
            rows: 4,
          },
        ],
      },
      procInstType: null,
      //附件设置
      attachSetting: {
        title: "",
        select: false,
        billType: this.procInstType, //页面标识
        sourceId: "", //单据的ID
        btnArr: ["add", "delete", "save"],
      },
      //评分设置
      scoreSetting: {
        title: "",
        billType: this.procInstType, //页面标识
        sourceId: "", //单据的ID
        data: [],
        btnArr: ["save"],
      },
      scoreHideBtn: false,
      coverList: [],
      dialogVisible: false,
      activeName: "0",
      hideBtn: false,
      showScore: false,
      maxNum:0,
      beforeSubmit: {
        //工作流提交前检测
        methods: (data) => {
          return this.submitBefore(data);
        },
      },
    };
  },
  computed: {
    //工作流额外参数
    workFlowParams() {
      let { procInstType } = this.editFormData;
      if (this.formData.status === 4 && this.formData.workflowIsApprover) {
        return {
          procInstType: procInstType || "",
          assignercodes: [this.editFormData.confirmMan],
        };
      }
      return { procInstType: procInstType || "" };
    },
    //是否是流程中可编辑
    isWorkFlowEdit() {
      if (this.formData.status === 4 && this.formData.workflowIsApprover) {
        return true;
      } else {
        return false;
      }
    },
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      //row.status
      if (this.pageType == "add") {
        let defaultForm = {};
        if (this.defaultSet) {
          defaultForm = { ...this.formData };
        }
        this.editFormData = Object.assign({}, defaultForm, {
          billDate: this.$utils.getNowDay(),
          qty:1
        });
      } else {
        this.getDetails(this.formData.id);
      }
      if (this.pageType == "details") {
        if (!this.isWorkFlowEdit) {
          this.formConfig.itemArr.map((i) => (i.disabled = true));
        }
        this.attachSetting.sourceId = this.formData.id; //附件id
      }
      if (this.pageType == "score") {
        //评分
        this.scoreSetting.sourceId = this.formData.id;
        this.formConfig.itemArr.map((i) => (i.disabled = true));
        this.showScore = true; //显示评分
      }
      // console.log(this.$store.getters.userInfo);
    },

    //获取详情
    async getDetails(id) {
      let res = await getDetailsApi(id);
      let obj = res.data || {};
      this.procInstType = obj.procInstType; //附件页面标识
      this.attachSetting.sourceId = obj.id; //附件对应的流程ID，新建流程保存后即可添加附件
      // console.log(res.data );
      this.editFormData = Object.assign({}, obj, {
        confirmMan: obj.empId,
        confirmManName: obj.zystaffunName,
      });
    },
    //提交前检测
    submitBefore(data) {
      //如果是起草节点
      if (this.isWorkFlowEdit) {
        return new Promise((resolve, reject) => {
          this.$refs.editForm
            .getFormData()
            .then((data) => {
              this.$set(data, "submitType", 1);
              this.editFormData = Object.assign({}, data);

              updateStorage(data)
                .then((res) => {
                  resolve();
                })
                .catch((e) => {
                  reject(e);
                });
            })
            .catch((e) => {
              console.log(e);
              reject("表单信息未填写完整");
            });
        });
      } else {
        return true;
      }
    },
    //工作流提交成功之后
    workFlowSuccess() {
      this.$emit("update:pageType", "list");
    },
    //获取表单数据
    getFormData() {
      return this.$refs.editForm.getFormData();
    },
    //点击选择入库单号
    getGoodsModel() {
      if (this.pageType == "details") {
        return;
      }
      let sourceId = this.$refs.editForm.getFormItem("sourceId");
      this.hadSeleteData = [{ id: sourceId }];

      this.showSelete = true;
    },
    //确定选择入库单号
    selectInstockId(list) {
      let [obj] = list;
      console.log(list);
      let {
        orderNo,
        assetTypeName,
        assetTypeId,
        goodsModel,
        id,
        qty
      } = obj;
      this.maxNum = qty;//备件的库存数量，用于控制领用数量的最大值
      // this.$refs.editForm.setFormItem("maxNum", qty);//库存数量，展示给用户看，作为领用数量填写的参考
      this.$refs.editForm.setFormItem("goodsModel", goodsModel);//备件型号
      this.$refs.editForm.setFormItem("instockId", id);//备件ID
      this.$refs.editForm.setFormItem("orderNo", orderNo);//入库单号
      this.$refs.editForm.setFormItem("assetTypeName", assetTypeName);//备件类型
      this.$refs.editForm.setFormItem("assetTypeId", assetTypeId);//备件类型ID
      // let {
      //   goodsModel,
      //   instockNo,
      //   orderNo,
      //   id
      // } = obj; 
      // this.$refs.editForm.setFormItem("goodsModel", goodsModel);
      // this.$refs.editForm.setFormItem("instockNo", instockNo);
      // this.$refs.editForm.setFormItem("orderNo", orderNo);
    },
      //点击选择服务工单号
    getServiceType() {
      // console.log("diaoyong");
      if (this.pageType == "details") {
        return;
      }
      let sourceId = this.$refs.editForm.getFormItem("sourceId");
      this.hadSeleteData = [{ id: sourceId }];

      this.serviceNumberSelete = true;
    },
    //确定选择服务工单号
    changeServiceType(list) {
      console.log(list);
      let [obj] = list;
      let { id, orderNo, procInstType } = obj; //assetsNumber
      this.$refs.editForm.setFormItem("serviceOrderNo", orderNo); //服务工单号
      this.$refs.editForm.setFormItem("serviceId", id); //服务工单ID
      this.$refs.editForm.setFormItem("serviceType", procInstType); //服务类型
    }
  },
};
</script>

<style scoped lang="scss">
</style>