import request from '@/request/api'
/**===========基本信息接口 */
//获取列表
export function getListApi(data = {}) {
    return request({
        url: '/maintain/SpareOutstockAction/queryList.htm',
        method: 'post',
        data: data
    })
}
//详情
export function getDetailsApi(id = "") {
    return request({
        url: '/maintain/SpareOutstockAction/detail.htm',
        method: 'post',
        params: { id: id }
    })
}
//删除
export function deleteOutstock(id = "") {
    return request({
        url: '/maintain/SpareInstockAction/delete.htm',
        method: 'post',
        params: { id: id }
    })
}

//提交流程
export function addOutstock(data = {}) {
    return request({
        url: '/maintain/SpareOutstockAction/addSpareOutstock.htm',
        method: 'post',
        data: data
    })
}
//保存草稿
export function updateOutstock(data = {}) {
    return request({
        url: '/maintain/SpareOutstockAction/updateSpareOutstock.htm',
        method: 'post',
        data: data
    })
}


